body {
    padding: 0;
    margin: 0;
    box-sizing: border-box;
    /* required for background in exchange component*/
    background: #132f53;
    overflow-x: hidden;
}

.app {
    color: whitesmoke;
    width: 100vw;
    height: 100vh;
}

.parentDiv {
    display: grid;
    place-items: center;
    grid-template-columns: repeat(3, 1fr);
    width: 100vw;
    height: 100vh;
}

.wrap {
    width: 100%;
    height: auto;
    display: grid;
    grid-template-columns: 1fr;
    place-items: center;
    background: #132f53;
}

.currencyApp {
    width: 75%;
}

.currencies {
    border-bottom: 1px solid gray;
    color: whitesmoke;
    width: 100%;
    height: auto;
    display: flex;
    flex-direction: column;

}

.currencies div {
    border-bottom: 1px solid lightgray;
    padding: 5px;
    display: grid;
    grid-template-columns: 30px 50px 1fr 200px;
    justify-items: space-between;
    align-items: center;
}

.currencies p {
    color: gray;
    float: right;
}

input[type=number] {
    padding: 15px;
    width: 250px;
}

.downloadBtn {
    padding: 15px;
    margin-left: 10px;
    background: #1abc9c;
    border: 0px;
    color: white;
    font-weight: 400;
}

.downloadBtn:hover {
    background: #9b59b6;
    cursor: pointer
}


h1 {
    font-family: 'Montserrat', sans-serif;
    font-weight: 200;
    color: whitesmoke;
    font-size: 4rem;
}

span {
    font-family: 'Montserrat', sans-serif;
    font-weight: 600;
    text-decoration: underline;
}

.sign {
    color: skyblue;
    padding: 10px;
    margin: 10px;
    text-decoration: none;
}

img {
    padding-right: 5px;
}

code {
    margin-top: 10px;
    background: tomato;
    padding: 3px;
    margin: 3px;
}